<template>
<div class="main">
		<div class="classify">
		<router-link v-for="classifylist in classifyData"
				:key="classifylist.id" 
				:to="`/classify/${classifylist.id}`" 
				tag="div" 
				class="sidebar">
				{{classifylist.name}}</router-link>	
		</div>		
			<!--嵌套路由 二级路由-->							
				<router-view/>
				
</div>
</template>

<script>
	export default{
		name:"classify",
		data(){
			return{
				classifyData:[]
			}
		},
		/*钩子函数 created在组件实例初始化时期调用,自动走这步,一般ajax请求都放在created里
		如果我们用methods方法就没有触发及调用源不能自动执行里面的方法*/
		created(){
			this.$http.getclassify().then(res=>{
				
				this.classifyData=res.data.res_body.classify
				/*异步请求的数据如何同步渲染出来：
				 * list渲染的数据为异步请求 页面加载进来的时候不能同步加载出来.解决$nextTick()
				$nextTick()数据修改更新后 立即使用这个方法得到更新后的dom*/
				this.$nextTick(()=>{
//					编程式导航
					this.$router.push(`/classify/${this.classifyData[0].id}`)
				})
			})
		}
	}
</script>

<style scoped="scoped">
	/*.main{
		display: flex;
	}*/
	.classify{
		display: flex;
		
	}
	.sidebar{
		height: 50px;
		width: 33.33%;
		text-align: center;
		line-height: 50px;
		background-color: gainsboro;
	}
	/*每点击一个分类按钮就会自动添加一个router-link-exact-active的class类名,利用这点添加背景色*/
	.router-link-exact-active{
		background-color: wheat;
	}
	.homelists{
		display: flex;
		flex-wrap: wrap;
	}
</style>


